/*********************variables***********************/
/*********************universal style***********************/
.center-parent, .panel .filter-radios {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: 10px; }

.center-child, .panel .filter-radios .radio-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-left: 10px; }

/*********************custome style***********************/
.panel {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  min-width: 40%;
  display: inline-block;
  box-sizing: border-box; }
  .panel .panel-heading input[type="checkbox"] {
    border: #fff; }
  .panel h3 {
    display: inline-block; }
  .panel .keywords {
    position: relative; }
    .panel .keywords .tag {
      display: inline-block;
      margin-left: 16px; }
    .panel .keywords .btn-group {
      float: right; }
    .panel .keywords .label, .panel .keywords .label {
      background-color: none;
      display: inline-block; }

.panel + .panel {
  margin-left: 30px; }

.panel:nth-child(2n-1) {
  margin-left: -30px; }



