@import url(/static/libs/bootstrap.min.css);
body, html, ul {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

a {
  text-decoration: none;
  color: #666; }

button {
  outline: none;
  background: none;
  border: none; }

table {
  border-collapse: collapse; }

.clearfix {
  display: inline-block;
  zoom: 1; }
  .clearfix:after {
    content: "";
    display: block;
    clear: both; }

.left {
  float: left; }

.right {
  float: right; }

html {
  height: 100%; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  width: 100%; }

.container {
  width: 80%;
  margin: 0 auto; }

.header-wrapper {
  width: 100%;
  margin: 0 auto; }

.content {
  width: 100%;
  margin: 0 auto;
  min-height: 450px; }

.footer {
  text-align: center; }



