/*********************variables***********************/
/*********************universal style***********************/
.active {
  background-color: #42b983;
  color: #fff; }

.center-parent, .panel .filter-radios {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: 10px; }

.center-child, .panel .filter-radios .radio-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-left: 10px; }

/*********************custome style***********************/
.global-actions {
  vertical-align: middle;
  width: 100%;
  line-height: 40px;
  padding: 40px 10px;
  box-sizing: border-box; }
  .global-actions .search-box {
    float: left; }
  .global-actions .file-btn-group {
    width: 40%;
    float: right;
    padding-top: 15px; }

.form-actions {
  margin-top: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }
  .form-actions .action-button {
    border: 1px solid #42b983;
    border-radius: 3px;
    font: 14px "simHei";
    padding: 8px 20px;
    display: inline-block; }
    .form-actions .action-button:hover {
      background-color: #eee; }
    .form-actions .action-button:focus {
      background-color: #42b983;
      color: #fff; }

.title-panels {
  margin-top: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-left: 30px; }

.panel {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  min-width: 40%;
  display: inline-block;
  box-sizing: border-box; }
  .panel .panel-heading input[type="checkbox"] {
    border: #fff; }
  .panel h3 {
    display: inline-block; }
  .panel .keywords {
    position: relative; }
    .panel .keywords .tag {
      display: inline-block;
      margin-left: 16px; }
    .panel .keywords .btn-group {
      float: right; }
    .panel .keywords .label, .panel .keywords .label {
      background-color: none;
      display: inline-block; }

.panel + .panel {
  margin-left: 30px; }

.panel:nth-child(2n-1) {
  margin-left: -30px; }

h1 {
  color: #42b983; }

.tag {
  display: inline-block;
  margin-left: 16px; }



